<template>
  <div class="app-container">
    <div class="filter-container">
      <el-date-picker
        v-model="date"
        type="date"
        class="filter-item"
        size="small"
        placeholder="请选择日期"
      />
      <el-select
        v-model="listQuery.status"
        class="filter-item"
        placeholder="状态"
        size="small"
        clearable
      >
        <el-option
          v-for="item in statusTypeOptions"
          :key="item.value"
          :label="item.lable"
          :value="item.value"
        />
      </el-select>
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        size="small"
        @click="handleFilter"
      >搜索</el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      stripe
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="序号" width="75">
        <template slot-scope="scope">{{ scope.$index }}</template>
      </el-table-column>
      <el-table-column label="通道名称" align="center">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </el-table-column>
      <el-table-column label="日期" align="center">
        <template
          slot-scope="scope"
        >{{ scope.row.ctime | timestampFilter("{y}-{m}-{d} {h}:{i}:{s}") }}</template>
      </el-table-column>
      <el-table-column label="价格" align="center">
        <template slot-scope="scope">
          <el-tag type="success" size="small">{{ scope.row.price ? (scope.row.price / 100) : 0 }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column class-name="status-col" label="状态" align="center">
        <template slot-scope="scope">
          <el-tag :type="scope.row.status | statusTagFilter" size="small">
            {{
            scope.row.status | statusFilter
            }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="耗时（毫秒）" align="center">
        <template slot-scope="scope">{{ scope.row.cost }}</template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
  </div>
</template>

<script>
import { taskList } from "@/api/supplier";
import { parseTime } from "@/utils";
import waves from "@/directive/waves"; // waves directive
import Pagination from "@/components/Pagination";

const statusTypeOptions = [
  { value: 4, lable: "成功" },
  { value: 5, lable: "失败" }
];

const statusTypeKeyValue = statusTypeOptions.reduce((acc, cur) => {
  acc[cur.value] = cur.lable;
  return acc;
}, {});

export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    statusFilter(status) {
      return statusTypeKeyValue[status];
    },
    statusTagFilter(status) {
      if (status === 5) {
        return "danger";
      } else {
        return "success";
      }
    },
    timestampFilter(time, fm) {
      return parseTime(time, fm);
    }
  },
  data() {
    return {
      statusTypeOptions,
      total: 0,
      listQuery: {
        status: undefined,
        dateStr: "",
        pageNo: 1,
        pageSize: 20
      },
      date: new Date(),
      list: null,
      listLoading: true
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      this.listQuery.dateStr = parseTime(this.date, "{y}-{m}-{d}");
      taskList(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
        }
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.listQuery.pageNo = 1;
      this.fetchData();
    }
  }
};
</script>
